#Top{
    .top-mod{
        min-height: 50vh;
        text-align: center;

        h2{
            font-family: SubTitles;
            font-size: 4em;
        }
    }
    .about{
        background-color: #00b8ff;
        padding-top: 60px;
        p{
            font-family: Text;
            color: #ffffff;
            font-size: 1.2em;
        }
    }
    .about-image{
        background-color: #ed543c;
        overflow: hidden;
        height: 50vh;
        padding: 0;
        img{
            position: relative;
            top:-80px;
        }
    }
    .skills{
        background-color: #32cc33;

        p{
            color: #ffffff;
            font-size: 1.5em;
            line-height: 0.7em;
            font-family: SubTitles;

            span{
                font-size: 0.6em;
                font-family: Text;
            }
        }

        .skillsText{
            font-size: 1em;
            line-height: 1;
        }
    }
    .skills-image{
        background-color: #15e9ce;
        overflow: hidden;
        height: 50vh;
        padding: 0;
        img{
            position: relative;
            top:-80px;
        }
    }
}