@font-face {
  font-family: "Titles";
  src: url(./Fonts/Poppins/Poppins-Bold.ttf);
}
@font-face {
  font-family: "SubTitles";
  src: url(./Fonts/Poppins/Poppins-Medium.ttf);
}
@font-face {
  font-family: "Text";
  src: url(./Fonts/Poppins/Poppins-ExtraLight.ttf);
}
html {
  background-color: #ffffff;
}
html body h1 {
  color: #ffffff;
}
html body h2 {
  color: #ffffff;
}
html body header {
  background-color: #000000;
  height: 50vh;
}
html body header h1 {
  font-size: 4vw;
  line-height: 1em;
  font-weight: bold;
  text-align: center;
  letter-spacing: 6px;
  font-family: Titles;
  color: transparent;
  -webkit-text-stroke: 1px #00dbde;
}
html body header h1 small {
  width: 100%;
  font-size: 0.5em;
  text-shadow: none;
  font-weight: 100;
  color: #00dbde;
  text-shadow: 0 0 4px #00dbde;
  -webkit-text-stroke: 0;
}/*# sourceMappingURL=App.css.map */