#Web{
    padding-top: 60px;
    background-color: #ffffff;

    .web-title{
        max-height: 30px;
        color: #333333;

        h2{
            line-height: 0.5;
            color: #333333;
            font-size: 3em;

            small{
                font-size: 0.5em;
                color: #7b2649;
            }
        }
    }

    .web-mod{
        text-align: justify;
        color: #505050;
        min-height: 300px;

        .web-mod-item{
            padding-left: 60px;
            padding-right: 60px;

            h3{
                color: #333333;
            }
            &:hover{
                h3{
                    color: #7b2649;
                }
            }
        }
    }

}